body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  width: calc(100% - 40px);
  max-width: 800px;
  margin: auto;
}

.links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
}

@media (max-width: 750px) {
  .links {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .link-container + .link-container {
    margin-top: 20px;
  }
}


